import request from "@/utils/request";
// URL
const MENU_BASE_URL = "/api/v1/menus";

class MenuAPI {
  /**
   * 
   * <p/>
   * token
   *
   * @returns 
   */
  static getRoutes(data) {
    return request<any, RouteVO[]>({
      url: `/api/gm_user/info`,
      method: "post",
      data:data
    });
  }

}

export default MenuAPI;

import { MenuTypeEnum } from "@/enums/MenuTypeEnum";

/**  */
export interface MenuQuery {
  /**  */
  keywords?: string;
}

/**  */
export interface MenuVO {
  /**  */
  children?: MenuVO[];
  /**  */
  component?: string;
  /** ICON */
  icon?: string;
  /** ID */
  id?: number;
  /**  */
  name?: string;
  /** ID */
  parentId?: number;
  /**  */
  perm?: string;
  /**  */
  redirect?: string;
  /**  */
  routeName?: string;
  /**  */
  routePath?: string;
  /** () */
  sort?: number;
  /**  */
  type?: MenuTypeEnum;
  /** (1:;0:) */
  visible?: number;
}

/**  */
export interface MenuForm {
  /** ID */
  id?: string;
  /** ID */
  parentId?: number;
  /**  */
  name?: string;
  /** (1- 0-) */
  visible: number;
  /** ICON */
  icon?: string;
  /**  */
  sort?: number;
  /**  */
  routeName?: string;
  /**  */
  routePath?: string;
  /**  */
  component?: string;
  /**  */
  redirect?: string;
  /**  */
  type?: MenuTypeEnum;
  /**  */
  perm?: string;
  /**  */
  keepAlive?: number;
  /**  */
  alwaysShow?: number;
  /**  */
  params?: KeyValue[];
}

interface KeyValue {
  key: string;
  value: string;
}

/** RouteVO */
export interface RouteVO {
  /**  */
  children: RouteVO[];
  /**  */
  component?: string;
  /**  */
  meta?: Meta;
  /**  */
  name?: string;
  /**  */
  path?: string;
  /**  */
  redirect?: string;
}

/** Meta */
export interface Meta {
  /**  */
  alwaysShow?: boolean;
  /** (true- false-) */
  hidden?: boolean;
  /** ICON */
  icon?: string;
  /**  */
  keepAlive?: boolean;
  /** title */
  title?: string;
}
