import request from "@/utils/request";

class AuthAPI {
  /**  */
  static login(data: LoginData) {
    return request({
      url: `/api/gm_user/login`,
      method: "post",
      data: data,
    });
  }

  /**  */
  static logout() {
    return request({
      url: `/api/gm_user/logout`,
      method: "delete",
    });
  }

}

export default AuthAPI;

/**  */
export interface LoginData {
  /**  */
  username: string;
  /**  */
  password: string;
}
