import defaultSettings from "@/settings";

//  Element Plus 
import zhCn from "element-plus/es/locale/lang/zh-cn";
import en from "element-plus/es/locale/lang/en";
import { store } from "@/store";
import { DeviceEnum } from "@/enums/DeviceEnum";
import { SidebarStatusEnum } from "@/enums/SidebarStatusEnum";

export const useAppStore = defineStore("app", () => {
  // 
  const device = useStorage("device", DeviceEnum.DESKTOP);
  // 
  const size = useStorage("size", defaultSettings.size);
  // 
  const language = window["lang"] //useStorage("language", defaultSettings.language);
  // 
  const sidebarStatus = useStorage("sidebarStatus", SidebarStatusEnum.CLOSED);
  const sidebar = reactive({
    opened: sidebarStatus.value === SidebarStatusEnum.OPENED,
    withoutAnimation: false,
  });

  // 
  const activeTopMenuPath = useStorage("activeTopMenuPath", "");

  /**
   * 
   */
  const locale = computed(() => {
    if (language == "en") {
      return en;
    } else {
      return zhCn;
    }
  });

  // 
  function toggleSidebar() {
    sidebar.opened = !sidebar.opened;
    sidebarStatus.value = sidebar.opened
      ? SidebarStatusEnum.OPENED
      : SidebarStatusEnum.CLOSED;
  }

  // 
  function closeSideBar() {
    sidebar.opened = false;
    sidebarStatus.value = SidebarStatusEnum.CLOSED;
  }

  // 
  function openSideBar() {
    sidebar.opened = true;
    sidebarStatus.value = SidebarStatusEnum.OPENED;
  }

  // 
  function toggleDevice(val: string) {
    device.value = val;
  }

  /**
   * 
   *
   * @param val  default | small | large
   */
  function changeSize(val: string) {
    size.value = val;
  }
  /**
   * 
   *
   * @param val
   */
  function changeLanguage(val: string) {
    language.value = val;
  }
  /**
   * 
   */
  function activeTopMenu(val: string) {
    activeTopMenuPath.value = val;
  }
  return {
    device,
    sidebar,
    language,
    locale,
    size,
    activeTopMenu,
    toggleDevice,
    changeSize,
    changeLanguage,
    toggleSidebar,
    closeSideBar,
    openSideBar,
    activeTopMenuPath,
  };
});

/**
 * Pinia Store  Pinia  store 
 *  Pinia Store
 * https://pinia.vuejs.org/core-concepts/outside-component-usage.html#using-a-store-outside-of-a-component
 */
export function useAppStoreHook() {
  return useAppStore(store);
}
