import axios, { InternalAxiosRequestConfig } from "axios";
import { useUserStoreHook } from "@/store/modules/user";
import { ResultEnum } from "@/enums/ResultEnum";
import { TOKEN_KEY } from "@/enums/CacheEnum";
import {getToken, removeToken, setToken} from "@/utils/auth";
import {disconnectCentrifuge} from '@/utils/centrifuge.js'
//  axios 
const service = axios.create({
  baseURL: getBaseURL(),
  timeout: 6000000,
  headers: { "Content-Type": "application/json;charset=utf-8" },
});

// 
service.interceptors.request.use(
  (config: InternalAxiosRequestConfig) => {
    config.headers["X-Token"] = getToken()

    config.headers["X-Version"] = window["appVersion"]

    if (localStorage.getItem('lang') != null) {
      config.headers['Current-Language'] = localStorage.getItem('lang')
    }

    return config;
  },
  (error: any) => {
    return Promise.reject(error);
  }
);

// 
service.interceptors.response.use(
  (response) => handleData(response),
  (error: any) => {

    const { response } = error
    if (response === undefined) {
      ElMessage({
        message: '', // error.message,
        type: 'error',offset:32,
        duration: 5 * 1000
      })
      return Promise.reject(error)
    } else return handleData(response)
  }
);

const handleData = (response) => {
  // 'blob'  'arraybuffer', 
  if (response.config.responseType == 'arraybuffer') {
    return response.data
  }

  const res = response.data

  // if the custom code is not 20000, it is judged as an error.
  if (res.code !== 0) {
    if (res.code === 40001) {
      ElMessage.error('!')
    }

    if (res.code === 40007){
      ElMessageBox.confirm(res.msg, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {
        location.reload();
      });

    }

    if (res.code === 40003 ) {

      let config = response.config
      if (!config._retry){

        let newToken = res.newToken
        setToken(newToken)
        config._retry = true
        config.headers["X-Token"] = getToken()
        config.headers["X-Version"] = window["appVersion"]
        return axios(config);
      }

      ElMessageBox.confirm('', "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {

        removeToken()
        disconnectCentrifuge()
        location.reload();
      });
    }

    if (res.code === 40008 ) {
      ElMessage.error(res.msg)

      removeToken()
      disconnectCentrifuge()
      location.reload();
    }

    if (res.code === 40002 ) {

      ElMessageBox.confirm('', "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning",
      }).then(() => {

        removeToken()
        disconnectCentrifuge()
        location.reload();
      });
    }
  }
  return res
}

//  axios 
export default service;

export function getBaseURL(){
  if(import.meta.env.PROD) return '/'

  return import.meta.env.VITE_APP_API_URL
}

